<template>
  <div class="content-box">
    <detail v-if="detailShow" :taskInfo="taskInfo" @back="detailShow=false"></detail>
    <div v-else>
      <div class="flex-spb mb-20">
        <div>
          时间: <DatePicker v-model="timeZone" type="daterange" :editable="false" placement="bottom-start"
            placeholder="请选择时间" style="width: 220px;margin-left:10px" @on-change="timeChange">
          </DatePicker>
          <span class="ml-20">订单编号：</span> <Input style="width: 220px;margin-left:10px" v-model="order_id"></Input>
          <Button type="primary" class="ml-10" @click="searchClick">搜索</Button>
        </div>
        <Button type="primary" @click="btnClick">新建任务</Button>
      </div>
      <Table :columns="columns" :data="data" border></Table>
      <div class="pageBox">
        <span>
          <span class="mr-10">共{{pageTotal}}条</span>
          每页{{pageSize}}条
        </span>
        <Page style="margin-right:120px" :total="pageTotal" :pageSize="pageSize" @on-change="pageChange"></Page>
        <span></span>
      </div>
    </div>
  </div>
</template>
<script>
import { dateFormate } from '@/utils/dateFormate.js'
import { mapGetters } from 'vuex'
import { number_formate } from '@/utils/number_formate.js'
import detail from './detail.vue'
export default {
  components: {
    detail
  },
  computed: {
    ...mapGetters(['userInfo'])
  },
  data () {
    return {
      detailShow: false,
      order_id: '',
      columns: [
        {
          title: '订单编号',
          key: 'id',
          width: 240
        },
        {
          title: '短信内容',
          key: 'context',
          width: 240,
          tooltip: true
        },
        {
          title: '订单金额',
          key: 'amount',
          width: 120,
          render: (h, params) => {
            return h('div', {}, number_formate(params.row.amount))
          },
        },
        {
          title: '实际消费',
          width: 120,
          render: (h, params) => {
            return h('div', {}, number_formate(params.row.pay_amount))
          },
        },
        {
          title: '提交时间',
          key: 'amount',
          align: 'center',
          width: 180,
          render: (h, params) => {
            if (params.row.create_time) {
              return h('div', {}, dateFormate(new Date(params.row.create_time * 1000), 'yyyy-MM-dd hh:mm:ss'))
            } else {
              return h()
            }
          },
        },
        {
          title: '审核时间',
          key: 'amount',
          align: 'center',
          width: 180,
          render: (h, params) => {
            if (params.row.approve_time) {
              return h('div', {}, dateFormate(new Date(params.row.approve_time * 1000), 'yyyy-MM-dd hh:mm:ss'))
            } else {
              return h('div', {}, '-')
            }
          },
        },
        {
          title: '成功',
          key: 'successful',
          width: 80,
        },
        {
          title: '失败',
          key: 'failure',
          width: 80,
        },
        {
          title: '未发送',
          key: 'failure',
          width: 80,
          render: (h, params) => {
            return h('div', {}, params.row.target_num - params.row.failure - params.row.successful)
          },
        },
        {
          title: '失败原因',
          key: 'error',
          width: 140,
          tooltip: true
        },
        {
          title: '总数',
          key: 'target_num',
          width: 80,
        },
        {
          title: '审核状态',
          key: 'amount',
          width: 100,
          render: (h, params) => {
            return h('div', {}, this.stateToStr(params.row.state))
          },
        },
        {
          title: '操作',
          align: 'center',
          minWidth: 80,
          render: (h, params) => {
            let h1 = null
            if (params.row.state != 3) {
              h1 = h('span', {}, '-')
            } else {
              h1 = h('a', {
                on: {
                  click: () => {
                    this.taskInfo = params.row;
                    this.detailShow = true
                  }
                }
              }, '详情')
            }
            return h1
          },
        },
      ],
      data: [],
      page: 1,
      pageSize: 10,
      pageTotal: 0,
      timeZone: '',
      id: ''
    }
  },
  mounted () {
    this.get_mission()
  },
  methods: {
    async get_mission () {
      let params = {
        pos: (this.page * 1 - 1) * this.pageSize,
        offset: this.pageSize,
        id: this.order_id.trim() ? this.order_id.trim() : undefined
      }
      if (this.timeZone[0]) {
        params.beg_create_time = this.timeZone[0].getTime() / 1000
        params.end_create_time = this.timeZone[1].getTime() / 1000
      }
      let resData = await this.$post('cgi/get_mission_entry', params)
      if (resData.ret_code == 0) {
        this.data = resData.ret_data.DataList ? resData.ret_data.DataList : []
        this.pageTotal = resData.ret_data.total_num
      }
    },
    timeChange () {
      let endTime = this.timeZone[1]
      if (endTime) {
        endTime.setHours(23)
        endTime.setMinutes(59)
        endTime.setSeconds(59)
        this.timeZone[1] = endTime
      }
      this.get_mission()
    },
    searchClick () {
      this.page = 1
      if ((this.order_id && this.order_id.length == 24) || !this.order_id) {
        this.get_mission()
      } else {
        this.data = []
        this.pageTotal = 0
      }
    },
    pageChange (page) {
      this.page = page;
      this.get_mission();
    },
    stateToStr (state) {
      let str = ''
      switch (state) {
        case -1:
          str = '系统处理中'
          break;
        case 0:
          str = '待审核'
          break;
        case 1:
          str = '已通过'
          break;
        case 2:
          str = '已驳回'
          break;
        case 3:
          str = '已结算'
          break;
        case 4:
          str = '已过期'
          break;
        default:
          break;
      }
      return str
    },
    btnClick () {
      if (this.userInfo.balance - this.userInfo.deposit == 0) {
        this.$Modal.info({
          title: '提示',
          content: '余额为0, 不可以发起新建任务'
        })
      } else {
        this.$router.push('new-task')
      }
    }
  }
}
</script>
<style lang="less" scoped>
/deep/ .ivu-table-cell {
  overflow: inherit;
  line-height: 20px;
}
</style>