<template>
  <div class="detail">
    <Spin fix v-if="loading"></Spin>
    <div style="width:600px" v-if="taskInfo">
      <p class="f20 fw mb-20"><a class="mr-20" @click="back">返回</a> 任务详情</p>
      <p>商户账号：{{taskInfo.mch_phone_num}}</p>
      <div class="flex-spb">
        <p>短信总数 {{taskInfo.target_num}}</p>
        <p>已发送{{taskInfo.successful+taskInfo.failure}}</p>
        <p>成功 {{taskInfo.successful}}</p>
        <p>失败 {{taskInfo.failure}}</p>
      </div>
      <p>
        下载文件：
        <Button class="ml-20 mr-20" @click="downloadFile(0)">未发送</Button>
        <Button class="mr-20" type="success" @click="downloadFile(1)">成功</Button>
        <Button class="mr-20" type="error" @click="downloadFile(2)">失败</Button>
      </p>
      <div class="flex mt-30">
        <span class="mr-12">短信内容：</span>
        <div class="msg-content">
          {{taskInfo.context}}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { dateFormate } from '@/utils/dateFormate'
export default {
  props: ['taskInfo'],
  data () {
    return {
      loading: false,
      state: '-1',
      states: [
        {
          type: '-1',
          name: '全部'
        },
        {
          type: '0',
          name: '未发送'
        },
        {
          type: '1',
          name: '成功'
        },
        {
          type: '2',
          name: '失败'
        },
      ],
    }
  },
  computed: {
    contact_list () {
      return this.stateData.slice((this.page - 1) * this.pageSize, this.page * this.pageSize)
    },
    sendNum () {
      return parseInt(this.taskInfo.successful + this.taskInfo.failure)
    },
    stateData () {
      let arr = []
      this.data.forEach(item => {
        if (this.state == -1) {
          arr.push(item)
        } else
          if (item.state == this.state) return arr.push(item)
      })
      return arr
    },
    // succ_rate () {
    //   if (this.sendNum) {
    //     return (Math.round(this.taskInfo.successful / this.sendNum * 10000) / 100.00 + '%')
    //   } else {
    //     return '0.00%'
    //   }

    // },
    // finish_rate () {
    //   return (Math.round(this.taskInfo.successful / this.data.length * 10000) / 100.00 + '%')
    // }
  },
  methods: {
    selectChange (state) {
      this.currPage = 1
      this.page = 1
      this.state = state
    },
    GetPercent (num, total) {
      num = parseFloat(num);
      total = parseFloat(total);
      if (isNaN(num) || isNaN(total)) {
        return "-";
      }
      return total <= 0 ? "0%" : (Math.round(num / total * 100) / 100.00);
    },
    downloadFile (type) {
      let params = {
        id: this.taskInfo.id,
        state: type
      }
      this.$post('cgi/get_mission', params, true, {
        responseType: 'blob'//服务器返回的数据类型
      }).then(response => {
        var blob = new Blob([response])
        var downloadElement = document.createElement('a');
        var href = window.URL.createObjectURL(blob); //创建下载的链接
        downloadElement.href = href;
        downloadElement.download = '用户数据.xlsx'; //下载后文件名
        document.body.appendChild(downloadElement);
        downloadElement.click(); //点击下载
        document.body.removeChild(downloadElement); //下载完成移除元素
        window.URL.revokeObjectURL(href); //释放掉blob对象
      })
    },
    getDetail () {
      this.loading = true
      this.$post('cgi/get_mission', { id: this.id }).then(resData => {
        this.loading = false
        if (resData.ret_code == 0) {
          this.taskInfo = resData.ret_data
          this.data = resData.ret_data.contact_list
        }
      }).catch(() => {
        this.loading = false
      })
    },
    back () {
      this.$emit('back')
    },
    dateFromat (time) {
      return dateFormate(new Date(time * 1000), 'yyyy-MM-dd hh:mm:ss')
    },
    pageChange (page) {
      this.page = page
    }
  }
}
</script>
<style lang="less" scoped>
.detail {
  .msg-content {
    margin-left: 10px;
    width: 500px;
    border: 1px solid #eeeeee;
    line-height: 18px;
    border-radius: 4px;
    min-height: 120px;
    padding: 10px;
    word-break: break-all;
  }
  p {
    line-height: 30px;
    margin: 10px 0;
  }
}
</style>